import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import CameraView from "./CameraView.js";
import GameScoreboard from "./scoreboards/GameScoreboard.js";
import { useOnlineGameStats } from "./scoreboards/useGameStats.js";
import { useMatch } from "../store/match.js";
import { useMatchControl } from "../store/matchControl.js";
import { usePendingVisit } from "../store/pendingVisit.js";
import PauseTimerBadge from "./ui/PauseTimerBadge.js";
import PauseQuitModal from "./ui/PauseQuitModal.js";
import PauseOverlay from "./ui/PauseOverlay.js";
import { useUserSettings } from "../store/userSettings.js";
import MatchStartShowcase from "./ui/MatchStartShowcase.js";
import { suggestCheckouts, sayScore } from "../utils/checkout.js";
import { getPreferredUserName } from "../utils/userName.js";
import { broadcastMessage } from "../utils/broadcast.js";
import AroundTheClockScorer, {
  type ATCDirection,
} from "./AroundTheClockScorer.js";
import { ATC_ORDER } from "../game/aroundTheClock.js";

export default function InGameShell({
  user,
  showStartShowcase: showStartShowcaseProp,
  onShowStartShowcaseChange,
  onCommitVisit,
  onQuit: onQuitProp,
  onPause: onPauseProp,
  onResume: onResumeProp,
  onPauseDurationSelected: onPauseDurationSelectedProp,
  onStateChange,
  localPlayerIndexOverride,
  gameModeOverride,
  isOnline,
  remoteStream,
}: {
  user: any;
  showStartShowcase?: boolean;
  onShowStartShowcaseChange?: (open: boolean) => void;
  /** Override default visit commit (for online WS sync). */
  onCommitVisit?: (score: number, darts: number, meta?: any) => void;
  /** Override default quit behaviour. */
  onQuit?: () => void;
  /** Override default pause behaviour (for online WS sync). */
  onPause?: () => void;
  /** Override default resume behaviour (for online WS sync). */
  onResume?: () => void;
  /** Called when pause duration is selected (for online WS sync). */
  onPauseDurationSelected?: (minutes: number, endsAt: number) => void;
  /** Called after any match state mutation for external sync. */
  onStateChange?: () => void;
  /** Explicit local player index for online (skips name matching). */
  localPlayerIndexOverride?: number;
  /** Override game mode label (e.g. online tracks currentGame separately). */
  gameModeOverride?: string;
  /** True when this is an online match â€” adds visual indicators. */
  isOnline?: boolean;
  /** Remote opponent camera stream for turn-by-turn camera alternation. */
  remoteStream?: MediaStream | null;
}) {
  const match = useMatch();
  useUserSettings((s: any) => s.hideInGameSidebar ?? true);
  useMatchControl((s: any) => s.setPaused);
  useMatchControl();
  usePendingVisit();

  const [winningShot, setWinningShot] = useState<{
    label?: string;
    ring?: string;
    frame?: string | null;
    ts?: number;
  } | null>(null);
  const [remoteFrame] = useState<string | null>(null);
  const remoteVideoRef = useRef<HTMLVideoElement>(null);

  // Attach remote opponent camera stream to the video element
  useEffect(() => {
    const v = remoteVideoRef.current;
    if (!v || !remoteStream) return;
    v.srcObject = remoteStream;
    v.play().catch(() => {});
    return () => {
      v.srcObject = null;
    };
  }, [remoteStream]);

  const lastOfflineStart = useUserSettings(
    (s: any) => s.lastOffline?.x01Start || 501,
  );

  const settingsUser = useUserSettings((s: any) => s.user);
  const localPlayerName =
    getPreferredUserName(user, "") ||
    getPreferredUserName(settingsUser, "") ||
    user?.email?.split("@")[0] ||
    settingsUser?.email?.split("@")[0] ||
    "You";

  const resolvedLocalIndex =
    typeof localPlayerIndexOverride === "number" &&
    localPlayerIndexOverride >= 0
      ? localPlayerIndexOverride
      : (match.players || []).findIndex(
          (p: any) => p?.name && p.name === localPlayerName,
        );
  const localPlayerIndex = resolvedLocalIndex >= 0 ? resolvedLocalIndex : 0;
  const isUsersTurn = (match.currentPlayerIdx ?? 0) === localPlayerIndex;

  const localPlayer = match.players?.[localPlayerIndex];
  const localLeg = localPlayer?.legs?.[localPlayer.legs.length - 1];
  const localRemaining =
    localLeg?.totalScoreRemaining ?? match.startingScore ?? lastOfflineStart;
  const awayIdx = (match.players || []).findIndex(
    (_p: any, idx: number) => idx !== localPlayerIndex,
  );
  const awayPlayer = (match.players || [])[awayIdx >= 0 ? awayIdx : 0];
  const awayLeg = awayPlayer?.legs?.[awayPlayer.legs.length - 1];
  const awayRemaining =
    awayLeg?.totalScoreRemaining ?? match.startingScore ?? lastOfflineStart;

  // Compute per-player stats for hero banner (last visit, 3-dart average)
  const playerStats = useMemo(() => {
    const compute = (p: any) => {
      if (!p) return { lastVisit: 0, avg3: 0 };
      let pts = 0,
        darts = 0;
      for (const L of p.legs || []) {
        pts += (L.totalScoreStart ?? 0) - (L.totalScoreRemaining ?? 0);
        darts += (L.visits || []).reduce(
          (a: number, v: any) => a + (v.darts || 0) - (v.preOpenDarts || 0),
          0,
        );
      }
      const lastV = (p.legs?.[p.legs.length - 1]?.visits || []).slice(-1)[0];
      return {
        lastVisit: lastV?.score ?? lastV?.visitTotal ?? 0,
        avg3: darts > 0 ? (pts / darts) * 3 : 0,
      };
    };
    return { local: compute(localPlayer), away: compute(awayPlayer) };
  }, [
    localPlayer,
    awayPlayer,
    localLeg?.visits?.length,
    awayLeg?.visits?.length,
  ]);

  // Checkout suggestions for the current thrower (local or away)
  const throwerRemaining = isUsersTurn ? localRemaining : awayRemaining;

  const gameMode = gameModeOverride || (match as any)?.game || "X01";
  const isX01 = gameMode === "X01";
  const scoreboardPlayers = useOnlineGameStats(gameMode, match as any);

  // Around the Clock state (per-player target tracking) â€” declared early
  // so heroValues and other memos can reference them.
  const isATC = gameMode === "Around the Clock";
  const [atcDirection, setAtcDirection] = useState<ATCDirection | null>(null);
  const [atcTargets, setAtcTargets] = useState<Record<number, number>>({});
  const atcOrder =
    atcDirection === "bull-to-1" ? [...ATC_ORDER].reverse() : ATC_ORDER;
  const localAtcIdx = atcTargets[localPlayerIndex] ?? 0;
  const awayAtcIdx = atcTargets[awayIdx >= 0 ? awayIdx : 1] ?? 0;

  // Game-mode-aware hero display values for each player
  const heroValues = useMemo(() => {
    const forPlayer = (p: any, sbIdx: number) => {
      const sb = scoreboardPlayers[sbIdx];
      if (isX01) {
        const leg = p?.legs?.[p.legs.length - 1];
        return {
          primary: String(
            leg?.totalScoreRemaining ?? match.startingScore ?? lastOfflineStart,
          ),
          primaryLabel: "Remaining",
        };
      }
      // Around the Clock â€” show target progress
      if (gameMode === "Around the Clock") {
        const pIdx =
          sbIdx === 0 ? localPlayerIndex : awayIdx >= 0 ? awayIdx : 1;
        const tIdx = atcTargets[pIdx] ?? 0;
        const order =
          atcDirection === "bull-to-1" ? [...ATC_ORDER].reverse() : ATC_ORDER;
        const tgt = order[tIdx];
        const label =
          tIdx >= order.length
            ? "Done!"
            : tgt === 25
              ? "Outer Bull"
              : tgt === 50
                ? "Inner Bull"
                : String(tgt);
        return {
          primary: tIdx >= order.length ? "âœ“" : label,
          primaryLabel:
            tIdx >= order.length
              ? "Complete"
              : `Target ${tIdx + 1}/${order.length}`,
        };
      }
      if (gameMode === "Cricket" || gameMode === "American Cricket") {
        return {
          primary: String(sb?.points ?? 0),
          primaryLabel: "Points",
        };
      }
      if (gameMode === "Killer") {
        const lives = sb?.lives ?? 0;
        return {
          primary: sb?.eliminated ? "OUT" : String(lives),
          primaryLabel: sb?.eliminated ? "Eliminated" : "Lives",
        };
      }
      // Generic score-based games
      return {
        primary: String(sb?.score ?? sb?.points ?? 0),
        primaryLabel: "Score",
      };
    };
    return {
      local: forPlayer(localPlayer, localPlayerIndex),
      away: forPlayer(awayPlayer, awayIdx >= 0 ? awayIdx : 0),
    };
  }, [
    isX01,
    gameMode,
    scoreboardPlayers,
    localPlayer,
    awayPlayer,
    localPlayerIndex,
    awayIdx,
    match.startingScore,
    lastOfflineStart,
    atcTargets,
    atcDirection,
  ]);

  const callerEnabled = useUserSettings((s: any) => s.callerEnabled);
  const callerVoice = useUserSettings((s: any) => s.callerVoice);
  const callerVolume = useUserSettings((s: any) => s.callerVolume);
  const speakCheckoutOnly = useUserSettings((s: any) => s.speakCheckoutOnly);

  const [showQuitPause, setShowQuitPause] = useState(false);
  const setPaused = useMatchControl((s: any) => s.setPaused);
  const paused = useMatchControl((s: any) => s.paused);
  const pauseEndsAt = useMatchControl((s: any) => s.pauseEndsAt);
  const pauseInitiator = useMatchControl((s: any) => s.pauseInitiator);
  const [pauseNow, setPauseNow] = useState(Date.now());

  // Signal the app shell to go full-screen on mobile while in-game
  useEffect(() => {
    document.documentElement.setAttribute("data-ndn-ingame", "true");
    return () => {
      document.documentElement.removeAttribute("data-ndn-ingame");
    };
  }, []);

  useEffect(() => {
    if (!paused || !pauseEndsAt) return;
    const t = setInterval(() => {
      const now = Date.now();
      setPauseNow(now);
      if (now >= pauseEndsAt) {
        setPaused(false, null);
      }
    }, 250);
    return () => clearInterval(t);
  }, [paused, pauseEndsAt, setPaused]);

  const checkoutRoutes = useMemo(() => {
    if (!isX01 || localRemaining > 170 || localRemaining <= 0) return null;
    const routes = suggestCheckouts(localRemaining);
    return routes && routes.length > 0 ? routes : null;
  }, [isX01, localRemaining]);

  // Checkout for the current thrower (shown in hero strip for both players)
  const throwerCheckout = useMemo(() => {
    if (!isX01 || throwerRemaining > 170 || throwerRemaining <= 0) return null;
    const routes = suggestCheckouts(throwerRemaining);
    return routes && routes.length > 0 ? routes : null;
  }, [isX01, throwerRemaining]);

  const numpadMax = isX01 ? 180 : 999;

  const legsInfo = useMemo(() => {
    const players = match.players || [];
    return players.map((p: any) => ({
      name: p.name || "Player",
      legsWon: p.legsWon || 0,
    }));
  }, [match.players]);

  // Only show the showcase when the parent explicitly tells us to AND
  // the match hasn't progressed yet (no visits recorded).
  const matchHasVisits = useMemo(() => {
    return (match.players || []).some((p: any) =>
      (p.legs || []).some((leg: any) => (leg.visits || []).length > 0),
    );
  }, [match.players]);
  const showStartShowcaseLocal =
    typeof showStartShowcaseProp === "boolean"
      ? showStartShowcaseProp && !matchHasVisits
      : false;
  const setShowStartShowcase = (open: boolean) => {
    onShowStartShowcaseChange?.(open);
  };

  const [showNumpad, setShowNumpad] = useState(false);
  const [numpadValue, setNumpadValue] = useState("");
  const numpadInputRef = useRef<HTMLInputElement>(null);

  // Auto-focus the input when numpad opens
  useEffect(() => {
    if (showNumpad && numpadInputRef.current) {
      numpadInputRef.current.focus();
    }
  }, [showNumpad]);

  // Turn timer: tracks how long the opponent has been throwing
  const [opponentTurnStart, setOpponentTurnStart] = useState<number | null>(
    null,
  );
  const [opponentTurnElapsed, setOpponentTurnElapsed] = useState(0);

  // Track when turn changes to start/stop timer
  useEffect(() => {
    if (!isUsersTurn) {
      // Opponent's turn started
      setOpponentTurnStart(Date.now());
    } else {
      // Our turn - reset
      setOpponentTurnStart(null);
      setOpponentTurnElapsed(0);
    }
  }, [isUsersTurn]);

  // Update elapsed time every second when opponent is throwing
  useEffect(() => {
    if (!opponentTurnStart) return;
    const interval = setInterval(() => {
      setOpponentTurnElapsed(
        Math.floor((Date.now() - opponentTurnStart) / 1000),
      );
    }, 1000);
    return () => clearInterval(interval);
  }, [opponentTurnStart]);

  // Format seconds as mm:ss
  const formatTurnTime = (seconds: number) => {
    const mm = Math.floor(seconds / 60)
      .toString()
      .padStart(2, "0");
    const ss = (seconds % 60).toString().padStart(2, "0");
    return `${mm}:${ss}`;
  };

  const commitVisit = (score: number, darts: number, meta?: any) => {
    const p = match.players?.[match.currentPlayerIdx];
    const leg = p?.legs?.[p.legs.length - 1];
    const prevRemaining = leg ? leg.totalScoreRemaining : match.startingScore;
    const numericScore = typeof score === "number" ? score : 0;
    let newRemaining = prevRemaining - numericScore;
    if (!Number.isFinite(newRemaining) || newRemaining < 0) newRemaining = 0;
    const isCheckout = newRemaining === 0 && numericScore > 0;

    // Professional darts double tracking â€” enrich meta if not already set
    const visitMeta = { visitTotal: numericScore, ...(meta || {}) };
    if (visitMeta.doubleWindowDarts == null) {
      let dwd = 0;
      if (prevRemaining <= 50) {
        dwd = darts;
      } else if (prevRemaining <= 170) {
        dwd = isCheckout ? 1 : newRemaining <= 50 ? 1 : 0;
      }
      visitMeta.doubleWindowDarts = dwd;
    }
    if (visitMeta.finishedByDouble == null) {
      visitMeta.finishedByDouble = isCheckout;
    }

    if (onCommitVisit) {
      onCommitVisit(numericScore, darts, visitMeta);
      return;
    }

    if (callerEnabled) {
      const playerName = p?.name || getPreferredUserName(user, "Player");
      try {
        sayScore(playerName, numericScore, newRemaining, callerVoice, {
          volume: callerVolume,
          checkoutOnly: speakCheckoutOnly,
        });
      } catch {}
    }

    match.addVisit(numericScore, darts, visitMeta);
    if (isCheckout) {
      match.endLeg(numericScore);
    } else {
      match.nextPlayer();
    }
    onStateChange?.();
  };

  // Around the Clock hit/miss handlers (must be after commitVisit)
  // onDartHit: immediately advance target index for this player (single dart)
  const handleAtcDartHit = useCallback(() => {
    const playerIdx = match.currentPlayerIdx ?? localPlayerIndex;
    setAtcTargets((prev) => ({
      ...prev,
      [playerIdx]: (prev[playerIdx] ?? 0) + 1,
    }));
  }, [match.currentPlayerIdx, localPlayerIndex]);

  // onTurnComplete: called after all 3 darts â€” commit the visit with totals
  const handleAtcTurnComplete = useCallback(
    (hits: number, totalValue: number) => {
      commitVisit(totalValue, 3, {
        visitTotal: totalValue,
        atcHit: hits > 0,
        atcHits: hits,
      });
    },
    [],
  );

  const deriveWinningLabel = useCallback(() => {
    try {
      const legs: Array<{ ts: number; visit?: any; player?: any }> = [];
      (match.players || []).forEach((p: any) => {
        (p.legs || []).forEach((leg: any) => {
          if (leg?.finished) {
            const lastVisit = (leg.visits || []).slice(-1)[0];
            legs.push({
              ts: leg.endTime || Date.now(),
              visit: lastVisit,
              player: p,
            });
          }
        });
      });
      if (!legs.length) return null;
      const latest = legs.sort((a, b) => b.ts - a.ts)[0];
      const entry = (latest.visit?.entries || []).slice(-1)[0];
      if (entry?.label) return entry.label as string;
      if (entry?.ring === "DOUBLE" && typeof entry?.value === "number")
        return `Double ${entry.value / 2}`;
      if (typeof latest.visit?.visitTotal === "number")
        return `Checkout ${latest.visit.visitTotal}`;
    } catch {}
    return null;
  }, [match.players]);

  useEffect(() => {
    if (match.inProgress) {
      setWinningShot(null);
      return;
    }
    if (!winningShot?.label) {
      const derived = deriveWinningLabel();
      if (derived) setWinningShot((prev) => ({ ...prev, label: derived }));
    }
  }, [match.inProgress, deriveWinningLabel, winningShot?.label]);

  return (
    <div className="ndn-game-shell ndn-page ndn-ingame-active relative overflow-hidden">
      {/* â”€â”€ Ambient background glow â”€â”€ */}
      <div className="pointer-events-none absolute inset-0 overflow-hidden">
        <div className="absolute -top-32 -left-32 w-96 h-96 rounded-full bg-indigo-600/10 blur-[120px]" />
        <div className="absolute -bottom-32 -right-32 w-96 h-96 rounded-full bg-amber-500/8 blur-[120px]" />
      </div>

      {showStartShowcaseLocal && (
        <MatchStartShowcase
          open={showStartShowcaseLocal}
          players={(match.players || []) as any}
          onDone={() => {
            setShowStartShowcase(false);
          }}
          onRequestClose={() => {
            setShowStartShowcase(false);
          }}
          showCalibrationDefault={true}
        />
      )}

      {/* â”€â”€ Premium sticky header â”€â”€ */}
      <div className="z-20 mb-1 shrink-0">
        <div className="relative flex items-center justify-between gap-2 px-3 sm:px-5 py-1.5 rounded-2xl border border-white/10 bg-white/5 backdrop-blur-xl shadow-lg">
          <div className="pointer-events-none absolute inset-0 rounded-2xl bg-gradient-to-r from-indigo-500/10 via-transparent to-amber-500/10" />
          <div className="flex items-center gap-3 text-sm leading-none z-10">
            {isOnline && (
              <span className="px-2.5 py-1 rounded-full bg-emerald-500/20 text-emerald-200 border border-emerald-400/30 text-[10px] sm:text-xs font-semibold tracking-wide">
                ğŸŒ Online
              </span>
            )}
            <span className="px-3 py-1 rounded-full bg-indigo-500/20 text-indigo-200 border border-indigo-400/30 text-xs font-semibold tracking-wide">
              {gameMode}
              {isX01 ? ` / ${match.startingScore || lastOfflineStart}` : ""}
            </span>
            {legsInfo.length >= 2 && (
              <span className="font-bold text-white/90 text-sm tabular-nums">
                Legs: {legsInfo[0].legsWon} â€“ {legsInfo[1].legsWon}
              </span>
            )}
          </div>
          <div className="flex items-center gap-2 z-10">
            <PauseTimerBadge />
            <button
              className="px-4 py-1.5 rounded-xl text-sm font-semibold bg-rose-600/90 hover:bg-rose-500 text-white border border-rose-400/30 shadow-lg shadow-rose-500/20 transition-all"
              onClick={() => setShowQuitPause(true)}
            >
              â¸ Quit / Pause
            </button>
          </div>
        </div>
      </div>

      {/* â”€â”€ Hero scoreboard banner â€” big remaining scores â”€â”€ */}
      <div className="relative mb-1 rounded-2xl border border-white/10 overflow-hidden shrink-0">
        {/* Gradient backdrop */}
        <div className="absolute inset-0 bg-gradient-to-r from-indigo-950/80 via-slate-900/90 to-amber-950/80" />
        <div className="absolute inset-0 bg-[radial-gradient(ellipse_at_center,rgba(255,255,255,0.03),transparent_70%)]" />

        <div className="relative grid grid-cols-[minmax(0,1fr)_auto_minmax(0,1fr)] items-center py-1.5 px-3 sm:px-4">
          {/* Player 1 (local) */}
          <div className="flex flex-col items-center">
            <div className="flex items-center gap-1.5 mb-0.5">
              <div
                className={`text-xs font-bold uppercase tracking-widest ${isUsersTurn ? "text-emerald-400" : "text-slate-400"}`}
              >
                {localPlayer?.name || "You"}
              </div>
              {isX01 && (
                <span className="text-[10px] font-semibold text-slate-400 tabular-nums">
                  ({localPlayer?.legsWon || 0})
                </span>
              )}
              {isUsersTurn && (
                <span className="w-1.5 h-1.5 rounded-full bg-emerald-400 animate-pulse" />
              )}
            </div>
            <div
              className={`font-mono text-3xl sm:text-4xl font-black tabular-nums leading-none ${isUsersTurn ? "text-white" : "text-white/60"}`}
            >
              {heroValues.local.primary}
            </div>
            <div className="flex items-center gap-2 mt-0.5 text-[10px]">
              <span className="uppercase tracking-wider text-slate-500 font-medium">
                {heroValues.local.primaryLabel}
              </span>
              {playerStats.local.avg3 > 0 && (
                <span className="text-slate-400 tabular-nums">
                  Avg: {playerStats.local.avg3.toFixed(1)}
                </span>
              )}
            </div>
          </div>

          {/* Centre divider â€” VS */}
          <div className="flex flex-col items-center justify-center px-3">
            <div className="w-px h-3 bg-gradient-to-b from-transparent via-white/20 to-transparent" />
            <div className="text-xs font-black text-white/30 tracking-widest my-0.5">
              VS
            </div>
            <div className="w-px h-3 bg-gradient-to-b from-transparent via-white/20 to-transparent" />
          </div>

          {/* Player 2 (away) */}
          <div className="flex flex-col items-center">
            <div className="flex items-center gap-1.5 mb-0.5">
              <div
                className={`text-xs font-bold uppercase tracking-widest ${!isUsersTurn ? "text-amber-400" : "text-slate-400"}`}
              >
                {awayPlayer?.name || "Opponent"}
              </div>
              {isX01 && (
                <span className="text-[10px] font-semibold text-slate-400 tabular-nums">
                  ({awayPlayer?.legsWon || 0})
                </span>
              )}
              {!isUsersTurn && (
                <span className="w-1.5 h-1.5 rounded-full bg-amber-400 animate-pulse" />
              )}
            </div>
            <div
              className={`font-mono text-3xl sm:text-4xl font-black tabular-nums leading-none ${!isUsersTurn ? "text-white" : "text-white/60"}`}
            >
              {heroValues.away.primary}
            </div>
            <div className="flex items-center gap-2 mt-0.5 text-[10px]">
              <span className="uppercase tracking-wider text-slate-500 font-medium">
                {heroValues.away.primaryLabel}
              </span>
              {playerStats.away.avg3 > 0 && (
                <span className="text-slate-400 tabular-nums">
                  Avg: {playerStats.away.avg3.toFixed(1)}
                </span>
              )}
            </div>
          </div>
        </div>

        {/* Checkout suggestion strip â€” shows for whoever is currently throwing */}
        {throwerCheckout && (
          <div
            className={`relative border-t px-3 py-1 flex items-center gap-2 ${
              isUsersTurn
                ? "border-emerald-400/20 bg-emerald-500/10"
                : "border-amber-400/20 bg-amber-500/10"
            }`}
          >
            <span
              className={`text-[10px] sm:text-xs uppercase tracking-wider font-semibold whitespace-nowrap ${
                isUsersTurn ? "text-emerald-300/80" : "text-amber-300/80"
              }`}
            >
              Checkout {throwerRemaining}:
            </span>
            <div className="flex flex-wrap gap-1.5">
              {throwerCheckout.map((route: string, i: number) => (
                <span
                  key={i}
                  className={`px-2 py-0.5 rounded-lg text-xs sm:text-sm font-medium ${
                    isUsersTurn
                      ? "bg-emerald-500/20 border border-emerald-400/20 text-emerald-100"
                      : "bg-amber-500/20 border border-amber-400/20 text-amber-100"
                  }`}
                >
                  {route}
                </span>
              ))}
            </div>
          </div>
        )}
      </div>

      {/* â”€â”€ Main content â€” camera always visible + turn-sensitive controls â”€â”€ */}
      <div className="flex-1 min-h-0 overflow-y-auto pb-16">
        <div className="flex flex-col gap-2">
          {/* Scoreboard + Camera side by side */}
          <div className="grid grid-cols-1 md:grid-cols-[1fr_1fr] gap-2 min-h-0">
            {/* Scoreboard â€” left side */}
            <div className="relative rounded-2xl border border-white/10 bg-slate-950/70 shadow-2xl ring-1 ring-white/5 p-2 sm:p-3 overflow-hidden">
              <div
                className={`pointer-events-none absolute inset-0 bg-gradient-to-br ${isUsersTurn ? "from-emerald-500/[0.03]" : "from-white/[0.02]"} to-transparent`}
              />
              <div className="flex items-center justify-between mb-2">
                <h3 className="text-sm font-bold text-white/90 tracking-wide">
                  Scoreboard
                </h3>
                <div
                  className={`px-3 py-1 rounded-full text-[10px] sm:text-xs font-semibold ${
                    isUsersTurn
                      ? "bg-emerald-500/20 border border-emerald-400/30 text-emerald-300 animate-pulse"
                      : "bg-amber-500/20 border border-amber-400/30 text-amber-300"
                  }`}
                >
                  {isUsersTurn
                    ? "â— Your turn"
                    : `${awayPlayer?.name || "Opponent"}\u2019s turn`}
                </div>
              </div>
              <GameScoreboard gameMode={gameMode} players={scoreboardPlayers} />
            </div>

            {/* Camera â€” right side, matches scoreboard height */}
            <div className="relative rounded-2xl border border-white/10 bg-slate-950/70 shadow-2xl ring-1 ring-white/5 overflow-hidden flex flex-col">
              <div className="flex items-center justify-between px-3 py-1 border-b border-white/5 bg-white/5 shrink-0">
                <div className="flex items-center gap-2">
                  <div
                    className={`w-2 h-2 rounded-full shadow-lg animate-pulse ${isUsersTurn ? "bg-emerald-400 shadow-emerald-400/50" : "bg-amber-400 shadow-amber-400/50"}`}
                  />
                  <span className="text-xs font-semibold text-white/80">
                    {isUsersTurn
                      ? "Your Board"
                      : `${awayPlayer?.name || "Opponent"}\u2019s Board`}
                  </span>
                </div>
                <span
                  className={`text-[10px] font-medium ${isUsersTurn ? "text-emerald-300" : "text-amber-300"}`}
                >
                  {isUsersTurn ? "â— Live" : "â— Waiting"}
                </span>
              </div>
              <div className="relative flex-1 min-h-0 bg-black">
                <div
                  className={`h-full ${isUsersTurn || !remoteStream ? "" : "hidden"}`}
                >
                  <CameraView hideInlinePanels={true} forceAutoStart={true} />
                </div>
                {!isUsersTurn && remoteStream && (
                  <video
                    ref={remoteVideoRef}
                    autoPlay
                    playsInline
                    muted
                    className="w-full h-full object-contain"
                  />
                )}
                {!isUsersTurn && !remoteStream && (
                  <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                    <div className="text-center">
                      <div className="text-white/30 text-xs uppercase tracking-wider mb-1">
                        Opponent&apos;s camera not available
                      </div>
                      <div className="text-white/20 text-[10px]">
                        Showing your board instead
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>

          {/* Around the Clock â€” opponent's turn */}
          {!isUsersTurn && isATC && atcDirection && (
            <AroundTheClockScorer
              direction={atcDirection}
              onPickDirection={() => {}}
              currentTargetIndex={awayAtcIdx}
              isUsersTurn={false}
              onTurnComplete={() => {}}
              onDartHit={() => {}}
              completed={awayAtcIdx}
              playerName={awayPlayer?.name || "Opponent"}
            />
          )}

          {/* Around the Clock â€” user's turn */}
          {isUsersTurn && isATC && (
            <AroundTheClockScorer
              direction={atcDirection}
              onPickDirection={(dir) => setAtcDirection(dir)}
              currentTargetIndex={localAtcIdx}
              isUsersTurn={isUsersTurn}
              onTurnComplete={handleAtcTurnComplete}
              onDartHit={handleAtcDartHit}
              completed={localAtcIdx}
              playerName={localPlayer?.name || "You"}
            />
          )}

          {/* X01 and other modes: quick score buttons â€” only when user's turn */}
          {isUsersTurn && !isATC && (
            <div className="flex flex-wrap gap-1.5 justify-center">
              {(isX01
                ? [180, 140, 100, 85, 60, 45, 26, 0]
                : [100, 80, 60, 45, 26, 20, 10, 0]
              ).map((v) => (
                <button
                  key={v}
                  className="px-3 py-1.5 rounded-lg text-xs font-bold bg-white/5 hover:bg-white/10 active:bg-white/15 border border-white/10 text-white/90 transition-all active:scale-95"
                  onClick={() => commitVisit(v, 3, { visitTotal: v })}
                >
                  {v}
                </button>
              ))}
            </div>
          )}

          {/* Winning shot banner */}
          {winningShot?.label && (
            <div className="rounded-2xl border border-emerald-400/30 bg-emerald-500/10 px-4 py-3 text-center">
              <span className="text-xs text-emerald-400/70 uppercase tracking-wider font-semibold">
                {isX01 ? "Winning Double" : "Winning Shot"}
              </span>
              <div className="text-lg font-bold text-emerald-200 mt-0.5">
                {winningShot.label}
              </div>
            </div>
          )}
        </div>
      </div>

      {/* â”€â”€ Number Pad Modal (X01 and other numpad modes only) â”€â”€ */}
      {showNumpad && !isATC && (
        <div
          className="fixed inset-0 z-50 flex items-start justify-center pt-8"
          onClick={() => setShowNumpad(false)}
          onKeyDown={(e) => {
            if (e.key === "Escape") {
              setShowNumpad(false);
            }
          }}
        >
          {/* Backdrop */}
          <div className="absolute inset-0 bg-black/70 backdrop-blur-sm" />
          {/* Panel */}
          <div
            className="relative w-full max-w-sm mx-auto rounded-3xl border border-white/10 bg-slate-900/95 backdrop-blur-xl shadow-2xl p-4 sm:p-6 animate-[slideUp_0.2s_ease-out]"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Score input field â€” typeable on both mobile and PC */}
            <div className="mb-4 rounded-2xl bg-black/40 border border-white/10 p-4 text-center">
              <div className="text-[10px] uppercase tracking-widest text-slate-400 mb-1">
                Score
              </div>
              {/* Score input â€” auto-focused via useEffect */}
              <input
                ref={numpadInputRef}
                type="number"
                inputMode="numeric"
                pattern="[0-9]*"
                className="w-full bg-transparent font-mono text-5xl sm:text-6xl font-black text-white tabular-nums min-h-[3.5rem] leading-none text-center outline-none border-none [appearance:textfield] [&::-webkit-inner-spin-button]:appearance-none [&::-webkit-outer-spin-button]:appearance-none placeholder:text-white/20"
                placeholder="0"
                value={numpadValue}
                onChange={(e) => {
                  const raw = e.target.value.replace(/[^0-9]/g, "");
                  if (raw === "" || Number(raw) <= numpadMax) {
                    setNumpadValue(raw);
                  }
                }}
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    e.preventDefault();
                    const score = Math.max(
                      0,
                      Math.min(numpadMax, Number(numpadValue) || 0),
                    );
                    commitVisit(score, 3, { visitTotal: score });
                    setNumpadValue("");
                    setShowNumpad(false);
                  }
                }}
              />
              {numpadValue &&
                checkoutRoutes &&
                Number(numpadValue) === localRemaining && (
                  <div className="mt-2 text-xs text-emerald-300 font-semibold">
                    âœ“ Checkout!
                  </div>
                )}
            </div>

            {/* Numpad grid */}
            <div className="grid grid-cols-3 gap-2 mb-3">
              {[1, 2, 3, 4, 5, 6, 7, 8, 9].map((n) => (
                <button
                  key={n}
                  className="py-3.5 rounded-xl text-xl font-bold bg-white/5 hover:bg-white/10 active:bg-white/20 border border-white/10 text-white transition-all active:scale-95"
                  onClick={() => {
                    const next = numpadValue + String(n);
                    if (Number(next) <= numpadMax) setNumpadValue(next);
                    numpadInputRef.current?.focus();
                  }}
                >
                  {n}
                </button>
              ))}
              {/* Bottom row: backspace, 0, submit */}
              <button
                className="py-3.5 rounded-xl text-lg font-bold bg-white/5 hover:bg-white/10 active:bg-white/20 border border-white/10 text-white/60 transition-all active:scale-95"
                onClick={() => {
                  setNumpadValue((v) => v.slice(0, -1));
                  numpadInputRef.current?.focus();
                }}
              >
                âŒ«
              </button>
              <button
                className="py-3.5 rounded-xl text-xl font-bold bg-white/5 hover:bg-white/10 active:bg-white/20 border border-white/10 text-white transition-all active:scale-95"
                onClick={() => {
                  const next = numpadValue + "0";
                  if (Number(next) <= numpadMax) setNumpadValue(next);
                  numpadInputRef.current?.focus();
                }}
              >
                0
              </button>
              <button
                className="py-3.5 rounded-xl text-lg font-bold bg-emerald-600 hover:bg-emerald-500 active:bg-emerald-400 border border-emerald-400/30 text-white shadow-lg shadow-emerald-600/30 transition-all active:scale-95"
                onClick={() => {
                  const score = Math.max(
                    0,
                    Math.min(numpadMax, Number(numpadValue) || 0),
                  );
                  commitVisit(score, 3, { visitTotal: score });
                  setNumpadValue("");
                  setShowNumpad(false);
                }}
              >
                âœ“
              </button>
            </div>

            {/* Quick buttons inside numpad */}
            <div className="flex flex-wrap gap-2 justify-center mb-3">
              {(isX01
                ? [180, 140, 100, 85, 60, 45, 26, 0]
                : [100, 80, 60, 45, 26, 20, 10, 0]
              ).map((v) => (
                <button
                  key={v}
                  className="px-3 py-1.5 rounded-lg text-xs font-bold bg-white/5 hover:bg-white/10 border border-white/10 text-white/80 transition-all active:scale-95"
                  onClick={() => {
                    commitVisit(v, 3, { visitTotal: v });
                    setNumpadValue("");
                    setShowNumpad(false);
                  }}
                >
                  {v}
                </button>
              ))}
            </div>

            {/* Cancel */}
            <button
              className="w-full py-2.5 rounded-xl text-sm font-semibold text-white/50 hover:text-white/80 hover:bg-white/5 transition-all"
              onClick={() => setShowNumpad(false)}
            >
              Cancel
            </button>
          </div>
        </div>
      )}

      {/* â”€â”€ Fixed bottom score input bar (X01 and other numpad modes only) â”€â”€ */}
      {!isATC && !showNumpad && !showQuitPause && !paused && (
        <div className="fixed bottom-0 left-0 right-0 z-30 bg-slate-900/95 backdrop-blur-xl border-t border-white/10 px-3 py-2 safe-bottom">
          <div className="flex items-center gap-2 max-w-lg mx-auto">
            <input
              type="number"
              inputMode="numeric"
              pattern="[0-9]*"
              className={`flex-1 border rounded-xl px-4 py-3 font-mono text-xl font-bold text-center outline-none [appearance:textfield] [&::-webkit-inner-spin-button]:appearance-none [&::-webkit-outer-spin-button]:appearance-none placeholder:text-white/30 ${
                isUsersTurn
                  ? "bg-white/5 border-white/15 text-white focus:border-emerald-400/50 focus:ring-1 focus:ring-emerald-400/30"
                  : "bg-white/3 border-white/10 text-white/50"
              }`}
              placeholder={
                isUsersTurn ? "Type score..." : "Waiting for opponent..."
              }
              value={numpadValue}
              disabled={!isUsersTurn}
              onChange={(e) => {
                const raw = e.target.value.replace(/[^0-9]/g, "");
                if (raw === "" || Number(raw) <= numpadMax) {
                  setNumpadValue(raw);
                }
              }}
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  e.preventDefault();
                  const score = Math.max(
                    0,
                    Math.min(numpadMax, Number(numpadValue) || 0),
                  );
                  if (numpadValue !== "") {
                    commitVisit(score, 3, { visitTotal: score });
                    setNumpadValue("");
                  }
                }
              }}
            />
            <button
              className={`px-5 py-3 rounded-xl text-sm font-bold text-white border transition-all active:scale-95 whitespace-nowrap ${
                isUsersTurn && numpadValue !== ""
                  ? "bg-emerald-600 hover:bg-emerald-500 active:bg-emerald-400 border-emerald-400/30 shadow-lg shadow-emerald-600/20"
                  : "bg-slate-700 border-white/10 opacity-50 cursor-not-allowed"
              }`}
              disabled={!isUsersTurn || numpadValue === ""}
              onClick={() => {
                const score = Math.max(
                  0,
                  Math.min(numpadMax, Number(numpadValue) || 0),
                );
                commitVisit(score, 3, { visitTotal: score });
                setNumpadValue("");
              }}
            >
              Submit
            </button>
            {isUsersTurn && (
              <button
                className="px-3 py-3 rounded-xl text-sm font-semibold bg-white/5 hover:bg-white/10 text-white/70 border border-white/10 transition-all"
                onClick={() => {
                  setNumpadValue("");
                  setShowNumpad(true);
                }}
              >
                âŒ¨
              </button>
            )}
          </div>
        </div>
      )}

      {/* â”€â”€ Quit / Pause modal â”€â”€ */}
      {showQuitPause && (
        <PauseQuitModal
          onClose={() => setShowQuitPause(false)}
          onQuit={() => {
            setShowQuitPause(false);
            if (onQuitProp) {
              onQuitProp();
            } else {
              try {
                match.endGame();
              } catch {}
              try {
                window.dispatchEvent(new Event("ndn:match-quit"));
              } catch {}
            }
          }}
          onPause={() => {
            try {
              setPaused(true, null, localPlayerName);
            } catch {}
            try {
              broadcastMessage({
                type: "pause",
                pauseStartedAt: Date.now(),
                pauseInitiator: localPlayerName,
              });
            } catch {}
            // Notify opponent via WS (online matches)
            if (onPauseProp) onPauseProp();
            setShowQuitPause(false);
          }}
        />
      )}

      <PauseOverlay
        localPlayerName={localPlayerName}
        onResume={() => {
          setPaused(false, null);
          try {
            broadcastMessage({ type: "unpause" });
          } catch {}
          // Notify opponent via WS (online matches)
          if (onResumeProp) onResumeProp();
        }}
        onDurationSelected={(minutes, endsAt) => {
          // Notify opponent via WS when duration is selected (online matches)
          if (onPauseDurationSelectedProp)
            onPauseDurationSelectedProp(minutes, endsAt);
        }}
      />
    </div>
  );
}
